import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import './App.css';

const LEDGER_TYPES = [
  { value: 'İşletme', label: 'İşletme' },
  { value: 'Bilanço', label: 'Bilanço' },
  { value: 'Basit Usul', label: 'Basit Usul' },
];
const DECLARATION_TYPES = [
  'KDV',
  'Muhtasar',
  'Geçici Vergi',
  'Yıllık Gelir',
  'Kurumlar',
  'Ba-Bs',
  'Damga',
  'Diğer',
];
const MONTHS = [
  'Ocak', 'Şubat', 'Mart', 'Nisan', 'Mayıs', 'Haziran',
  'Temmuz', 'Ağustos', 'Eylül', 'Ekim', 'Kasım', 'Aralık'
];

function App({ allDeclarations = [] }) {
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [editCustomer, setEditCustomer] = useState(null);

  const openEditModal = (customer) => {
    setEditCustomer(customer);
    setEditModalOpen(true);
  };
  const closeEditModal = () => {
    setEditCustomer(null);
    setEditModalOpen(false);
  };

  const [name, setName] = useState('');
  const [taxNo, setTaxNo] = useState('');
  const [ledgerType, setLedgerType] = useState('İşletme');
  const [selectedDeclarations, setSelectedDeclarations] = useState([]);
  const [declarationMonths, setDeclarationMonths] = useState({});
  const [customers, setCustomers] = useState([]);
  const [successMsg, setSuccessMsg] = useState('');
  const [errorMsg, setErrorMsg] = useState('');

  useEffect(() => {
    axios.get('http://localhost:5000/api/customers')
      .then(res => setCustomers(res.data))
      .catch(() => setCustomers([]));
  }, []);

  const handleDeclarationChange = (type) => {
    setSelectedDeclarations(prev =>
      prev.includes(type)
        ? prev.filter(t => t !== type)
        : [...prev, type]
    );
  };

  const handleMonthChange = (type, month) => {
    setDeclarationMonths(prev => ({
      ...prev,
      [type]: prev[type]?.includes(month)
        ? prev[type].filter(m => m !== month)
        : [...(prev[type] || []), month],
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSuccessMsg(''); setErrorMsg('');
    if (!name || !taxNo || selectedDeclarations.length === 0) {
      setErrorMsg('Tüm alanları doldurun ve en az bir beyanname seçin.');
      return;
    }
    try {
      // 1. Müşteri ekle
      const res = await axios.post('http://localhost:5000/api/customers', {
        name, tax_no: taxNo, ledger_type: ledgerType
      });
      const customerId = res.data.id;
      // 2. Beyanname ve ay ekle
      for (const type of selectedDeclarations) {
        const months = declarationMonths[type] || [];
        for (const month of months) {
          await axios.post('http://localhost:5000/api/declarations', {
            customer_id: customerId,
            type,
            month: MONTHS.indexOf(month) + 1,
            year: new Date().getFullYear()
          });
        }
      }
      setSuccessMsg('Kayıt başarılı!');
      setName(''); setTaxNo(''); setLedgerType('İşletme');
      setSelectedDeclarations([]); setDeclarationMonths({});
      // Refresh customers
      const updated = await axios.get('http://localhost:5000/api/customers');
      setCustomers(updated.data);
    } catch (err) {
      setErrorMsg('Kayıt sırasında hata oluştu.');
    }
  };

  return (
    <div className="container">
      <h2>Müşteri Kayıt</h2>
      <form onSubmit={handleSubmit} className="form">
        <div>
          <label>Müşteri Adı</label>
          <input value={name} onChange={e => setName(e.target.value)} required />
        </div>
        <div>
          <label>Vergi No</label>
          <input value={taxNo} onChange={e => setTaxNo(e.target.value)} required />
        </div>
        <div>
          <label>Defter Türü</label>
          <select value={ledgerType} onChange={e => setLedgerType(e.target.value)}>
            {LEDGER_TYPES.map(opt => (
              <option key={opt.value} value={opt.value}>{opt.label}</option>
            ))}
          </select>
        </div>
        <div>
          <label>Tabi Olduğu Beyannameler</label>
          <div className="checkbox-group">
            {DECLARATION_TYPES.map(type => (
              <label key={type}>
                <input
                  type="checkbox"
                  checked={selectedDeclarations.includes(type)}
                  onChange={() => handleDeclarationChange(type)}
                /> {type}
              </label>
            ))}
          </div>
        </div>
        {selectedDeclarations.map(type => (
          <div key={type} className="months-select">
            <label>{type} için Aylar:</label>
            <div className="checkbox-group">
              {MONTHS.map(month => (
                <label key={month}>
                  <input
                    type="checkbox"
                    checked={declarationMonths[type]?.includes(month) || false}
                    onChange={() => handleMonthChange(type, month)}
                  /> {month}
                </label>
              ))}
            </div>
          </div>
        ))}
        <button type="submit">Kaydet</button>
        {successMsg && <div className="success">{successMsg}</div>}
        {errorMsg && <div className="error">{errorMsg}</div>}
      </form>
      <h3>Kayıtlı Müşteriler</h3>
      <ul>
        {customers.map(c => (
          <li key={c.id}>
            {c.name} - {c.tax_no} - {c.ledger_type}
            <button style={{marginLeft: 10, padding: '2px 10px', borderRadius: 4, border: 'none', background: '#007bff', color: '#fff', cursor: 'pointer'}} onClick={() => openEditModal(c)}>
              Düzenle
            </button>
          </li>
        ))}
      </ul>
      <EditDeclarationsModal
        open={editModalOpen}
        customer={editCustomer}
        declarations={allDeclarations}
        onSave={async (selectedDeclarations, declarationMonths) => {
          if (!editCustomer) return;
          // 1. Eski beyannameleri sil
          const customerDecls = allDeclarations.filter(d => d.customer_id === editCustomer.id);
          for (const decl of customerDecls) {
            await fetch(`http://localhost:5000/api/declarations/${decl.id}`, { method: 'DELETE' });
          }
          // 2. Yeni beyannameleri ekle
          const year = new Date().getFullYear();
          for (const type of selectedDeclarations) {
            const months = declarationMonths[type] || [];
            for (const month of months) {
              await fetch('http://localhost:5000/api/declarations', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                  customer_id: editCustomer.id,
                  type,
                  month: MONTHS.indexOf(month) + 1,
                  year
                })
              });
            }
          }
          closeEditModal();
          // DeclarationList'teki beyanname listesini güncelle
          window.location.reload(); // Hızlıca çözüm, daha iyi bir yöntem için fetchDeclarations fonksiyonu prop ile aktarılabilir
        }}
        onClose={closeEditModal}
      />
    </div>
  );
}

import DeclarationList from './DeclarationList';
import EditDeclarationsModal from './EditDeclarationsModal';

function AppWrapper() {
  // DeclarationList'ten beyanname verisini almak için ref kullanıyoruz
  const declListRef = useRef();
  const [allDeclarations, setAllDeclarations] = React.useState([]);

  // DeclarationList'ten beyanname listesini almak için callback
  const handleDeclarationsUpdate = (declarations) => {
    setAllDeclarations(declarations);
  };

  return (
    <>
      <App allDeclarations={allDeclarations} />
      <DeclarationList ref={declListRef} onDeclarationsUpdate={handleDeclarationsUpdate} />
    </>
  );
}

export default AppWrapper;
